import {ChangeDetectorRef, Component, ElementRef, OnInit, ViewChild, ViewEncapsulation} from '@angular/core';
import {IAllGood, IGoodPropertyTemplate, IGoodsCategory, IWarehouse, RVendor} from '../../../interfaces/interfaces';
import {Constants} from '../../../app.constants';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {AuthService} from '../../../services/auth.service';
import {HttpClient} from '@angular/common/http';
import {VendorGoodsService} from '../../../services/vendor-goods.service';
import {ActivatedRoute, Router} from '@angular/router';
import {AppData} from '../../../data';
import {UploadService} from '../../../services/upload.service';

@Component({
    selector: 'app-create-vendor-goods',
    templateUrl: './create-vendor-goods.component.html',
    styleUrls: ['./create-vendor-goods.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class CreateVendorGoodsComponent implements OnInit {

    public warehouses: IWarehouse[] = [];
    public selectedWareHouse: IWarehouse;
    public allGoods: IAllGood[] = [];
    public selectedGood: IAllGood;
    public selected = [];
    public newForm: FormGroup;
    public newGoodsForm: FormGroup;
    errorMessage?: string;
    saving: boolean;
    pictures: any[] = [];
    pictureIds: any[] = [];
    file = '';
    searching?: boolean;
    savingNewGoods = false;
    details: IGoodPropertyTemplate[] = [];
    timer?: any;

    newProperties?: IGoodPropertyTemplate[] = [];
    properties?: IGoodPropertyTemplate[] = [];
    categories?: IGoodsCategory[] = [];
    displayType = 'primary';
    vendors: RVendor[] = [];
    loadingWarehouse: boolean;
    propertyVal = '';
    canAddVendorsGoods = false;

    uploadResponse = {status: '', message: '', filePath: ''};
    uploadId = '';

    constructor(private formBuilder: FormBuilder,
                private router: Router,
                private route: ActivatedRoute,
                private goodsService: VendorGoodsService,
                private changeDetectorRef: ChangeDetectorRef,
                private elem: ElementRef,
                public http: HttpClient,
                private uploadService: UploadService) {
        this.canAddVendorsGoods = this.isVendor || this.can(this.permissions.add_goods);
        this.uploadId = 'create-vendor-goods-' + Constants.grabRandomID();
    }

    get isVendor() {
        return AppData.roles.vendor === (AuthService.getAuthenticatedUser() || {}).r;
    }

    get permissions() {
        return AppData.permissions;
    }

    can(permission) {
        return AuthService.can(permission);
    }

    public uploadFinished  (event: any) {
        this.pictures.push(event);
        this.pictureIds.push(event.id);
    }

    triggerUpload(id) {
        if (id === this.uploadId) this.uploadService.upload(id);
    }


    ngOnInit() {

        this.timer = null;
        this.saving = false;
        this.searching = false;
        this.loadingWarehouse = false;

        this.buildForm();
        this.getCategories();

        this.getWarehouse();
        if (this.isVendor) {
            this.updateVId();
        } else {
            this.loadVendors();
        }
    }

    private buildForm() {
        const d: any = {
            all_good_id: ['', Validators.compose([Validators.required])],
            searchField: ['', Validators.compose([Validators.required])],
            full_description: ['', Validators.compose([Validators.required, Validators.maxLength(255)])],
            price: ['', Validators.compose([Validators.required, Validators.maxLength(255)])],
            sku: [''],
            warehouse_id: ['', Validators.compose([Validators.required])]
        };
        if (!this.isVendor) {
            d.vendor_id = ['', Validators.compose([Validators.required])];
        }
        this.newForm = this.formBuilder.group(d);

        this.newGoodsForm = this.formBuilder.group({
            name: ['', Validators.compose([Validators.required])],
            category_id: ['', Validators.compose([Validators.required])],
            short_description: ['', Validators.compose([Validators.required])]
        });
    }

    loadVendors() {
        this.goodsService.getVendors().subscribe((vendors: RVendor[]) => {
            this.vendors = vendors;
        }, (err) => {
            Constants.handleError(err, this.router, true);
        });
    }

    selectionChanged(goodsDetail: IAllGood) {
        this.selectedGood = goodsDetail;
        if (this.selectedGood == null) {
            this.clearAdditionalFields();
        } else {
            this.newForm.patchValue({all_good_id: this.selectedGood.id});
            this.properties = goodsDetail.properties_template;
        }
    }

    private clearAdditionalFields() {
        this.selectedGood = null;
        this.newForm.patchValue({all_good_id: ''});

        this.properties = [];
    }

    onFileChange(event) {
        if (event.target.files.length > 0) {
            const file = event.target.files[0];
            this.newForm.get('picture').setValue(file);
        }
    }

    public getSearchResult(): void {
        if (this.timer != null) {
            clearTimeout(this.timer);
        }

        this.timer = setTimeout(() => {
            this.searching = true;
            this.errorMessage = '';
            this.goodsService.getIAllGoods(this.newForm.get('searchField').value).subscribe((returnedGoods: IAllGood[]) => {
                this.allGoods = returnedGoods;
                this.searching = false;
            }, (error) => {
                this.searching = false;
                this.errorMessage = Constants.handleError(error, this.router);
            });
        }, 400);
    }

    public getWarehouse(): void {
        this.loadingWarehouse = true;
        let subscriber: any;
        if (this.isVendor) {
            subscriber = this.goodsService.getWarehouse();
        } else {
            const vendor = this.newForm.get('vendor_id').value;
            if (!vendor && !this.isVendor) {
                this.warehouses = [];
                return;
            }
            subscriber = this.goodsService.getWarehouseFor(vendor);
        }

        subscriber.subscribe((warehouses: any) => {
                this.warehouses = warehouses;
                this.loadingWarehouse = false;
            }, (error) => {
                Constants.handleError(error, this.router, true);
                this.loadingWarehouse = false;
            }
        );
    }

    getCategories() {
        this.goodsService.getGoodsCategory().subscribe((categories: IGoodsCategory[]) =>
            this.categories = categories, (error) => Constants.handleError(error, this.router)
        );
    }

    deselectSelectedGood() {
        this.clearAdditionalFields();
    }

    onSubmit(form: { value: any }) {
        const formData = form.value;
        formData.pictures = this.pictureIds;
        formData.properties = JSON.stringify({data: this.properties});

        this.errorMessage = '';
        this.saving = true;
        this.goodsService.addNewVendorGood(Constants.url + 'goods', formData).subscribe((res: any) => {
            this.newForm.reset();
            this.properties = [];

            if (this.isVendor) {
                this.updateVId();
            }
            // this.fileInput.nativeElement.value = '';
            Constants.succeeded(res.message);
            this.saving = false;
            this.pictures = [];
            this.pictureIds = [];
        }, (error) => {
            Constants.handleError(error, this.router, true);
            this.saving = false;
        });
    }

    prepareToSaveNewGood() {
        const input = this.newGoodsForm.value;
        input.properties = JSON.stringify({data: this.newProperties});
        return input;
    }

    createNewGoodRecord() {
        if (this.saving) {
            return;
        }
        const formModel = this.prepareToSaveNewGood();
        this.savingNewGoods = true;

        this.errorMessage = '';
        this.goodsService.addNewAllGood(formModel).subscribe((res: any) => {
            Constants.succeeded(res.message);
            this.newGoodsForm.reset();
            this.newProperties = [];
            // this.newGoodsForm.reset();
            // this.newProperties = [];
            this.savingNewGoods = false;
        }, (error) => {
            this.errorMessage = Constants.handleError(error, this.router);
            this.savingNewGoods = false;
        });
    }

    addNewProperty() {
        this.newProperties.push({name: '', type: ''});
    }

    removeProperty(property ?: IGoodPropertyTemplate) {
        this.newProperties.splice(this.newProperties.indexOf(property), 1);
    }

    changeDisplay(type: string) {
        this.displayType = type;
    }

    addProperty(property: IGoodPropertyTemplate) {
        this.properties.push(property);
    }

    showProperties(cCategory) {
        const list = [];
        if (cCategory) {
            this.categories.forEach(category => {
                if (category.id.toString() === cCategory.toString()) {
                    category.properties_template.forEach(t => {
                        list.push(t.name);
                    });
                }
            });
        }

        let val = '';
        if (list.length > 0) {
            val = '<small class="mt-2">Properties: <strong>' + list.join(', ') + '</strong></small>';
        }
        this.propertyVal = val;
    }

    private updateVId() {
        const vendorId = (AuthService.getAuthenticatedUser() || {}).v;
        this.newForm.patchValue({vendor_id: vendorId});
    }
}
