import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {IAllGood, IGood, IGoodPropertyTemplate, IGoodsCategory, IPicture, IWarehouse} from '../../../interfaces/interfaces';
import {Constants} from '../../../app.constants';
import {FormBuilder, FormGroup, Validators} from '@angular/forms';
import {ActivatedRoute, Router} from '@angular/router';
import {HttpClient} from '@angular/common/http';
import {AuthService} from '../../../services/auth.service';
import {VendorGoodsService} from '../../../services/vendor-goods.service';
import {UploadService} from '../../../services/upload.service';
import {AppData} from '../../../data';
import Swal from 'sweetalert2';

@Component({
    selector: 'app-edit-vendor-goods',
    templateUrl: './edit-vendor-goods.component.html',
    styleUrls: ['./edit-vendor-goods.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class EditVendorGoodsComponent implements OnInit {

    public editForm: FormGroup;
    public newGoodsForm: FormGroup;
    url: string;
    public warehouses: IWarehouse[] = [];
    public warehouse: IWarehouse;
    allGoods: IAllGood[] = [];
    errorMessage: any;
    saving: boolean;
    searching: boolean;
    savingNewGoods = false;
    pageLoading: boolean;

    selectedVendorGood?: IGood = null;
    originalVendorGood?: IGood = null;
    selectedGood?: IGood = null;
    selectedAllGood?: IAllGood = null;
    selectedProperties?: IGoodPropertyTemplate[] = [];
    timer?: any;
    properties?: IGoodPropertyTemplate[] = [];
    pictures?: IPicture[] = [];
    pictureIds = [];

    newProperties?: IGoodPropertyTemplate[] = [];
    categories?: IGoodsCategory[] = [];
    displayType = 'primary';
    canAddVendorsGoods = false;
    private goodId: number;
    propertyVal = '';
    uploadId = '';

    constructor(private formBuilder: FormBuilder,
                private router: Router,
                private route: ActivatedRoute,
                private goodsService: VendorGoodsService,
                public http: HttpClient,
                private authService: AuthService,
                private uploadService: UploadService) {
        this.canAddVendorsGoods = this.isVendor || this.can(this.permissions.edit_goods);
        this.uploadId = 'edit-vendor-goods-' + Constants.grabRandomID();
    }

    ngOnInit() {
        this.url = Constants.url;
        this.goodId = this.route.snapshot.params['id'];
        this.buildForm();
        this.getDetailsForSelectedVendorGood();

    }

    get isVendor() {
        return AppData.roles.vendor === (AuthService.getAuthenticatedUser() || {}).r;
    }

    get permissions() {
        return AppData.permissions;
    }

    can(permission) {
        return AuthService.can(permission);
    }

    public uploadFinished(event: any) {
        this.pictures.push(event);
        this.pictureIds.push(event.id);
    }

    triggerUpload(id) {
        if (id === this.uploadId) this.uploadService.upload(id);
    }


    public getWarehouse(): void {
        this.goodsService.getWarehouseFor(this.selectedVendorGood.vendor_id).subscribe((warehouses: IWarehouse[]) => {
                this.warehouses = warehouses
            }, (err) => {
                this.errorMessage = Constants.handleError(err, this.router);
            }
        );
    }

    selectionChanged(goodsDetail: IAllGood) {
        this.selectedAllGood = goodsDetail;
        if (this.selectedAllGood == null) {
            this.clearAdditionalFields();
        } else {
            this.editForm.patchValue({all_good_id: this.selectedAllGood.id});
            this.properties = goodsDetail.properties_template;
        }
    }

    private clearAdditionalFields() {
        this.selectedAllGood = null;
        this.editForm.patchValue({all_good_id: ''});

        this.properties = [];
    }

    buildForm() {
        this.editForm = this.formBuilder.group({
            all_good_id: ['', Validators.compose([Validators.required])],
            searchField: ['', Validators.compose([Validators.required])],
            full_description: ['', Validators.compose([Validators.required, Validators.maxLength(255)])],
            price: ['', Validators.compose([Validators.required, Validators.maxLength(255)])],
            sku: [''],
            warehouse_id: ['', Validators.compose([Validators.required])]
        });

        this.newGoodsForm = this.formBuilder.group({
            name: ['', Validators.compose([Validators.required])],
            category_id: ['', Validators.compose([Validators.required])],
            short_description: ['', Validators.compose([Validators.required])]
        });

    }

    getDetailsForSelectedVendorGood() {
        this.pageLoading = true;
        this.goodsService.getGoodDetailByID(this.goodId, false).subscribe((good: IGood) => {
                console.log(good);
                this.updateNewGoodsDetail(good);
                this.getWarehouse();
                this.pageLoading = false
            }, (err) => {
                this.errorMessage = Constants.handleError(err, this.router, true);
                this.pageLoading = false;
            }
        );
    }

    private updateNewGoodsDetail(good: IGood) {
        this.originalVendorGood = good;
        this.selectedGood = good;
        this.selectedVendorGood = good;
        this.properties = [];
        this.selectedProperties = [];
        this.selectedAllGood = this.originalVendorGood.detail;

        this.pictureIds = [];
        this.pictures = this.selectedGood.pictures || [];
        this.pictures.forEach(pix => this.pictureIds.push(pix.id));

        this.editForm.patchValue({
            all_good_id: this.selectedAllGood.id,
            searchField: this.selectedAllGood.name,
            full_description: this.originalVendorGood.full_description,
            price: this.originalVendorGood.price,
            sku: this.originalVendorGood.sku,
            warehouse_id: this.originalVendorGood.warehouse_id
        });

        let hasProperties = false;
        if (this.selectedGood.properties !== undefined && this.selectedGood.properties && this.selectedGood.properties.length) {
            hasProperties = true;
        }

        if (this.selectedAllGood.properties_template && this.selectedAllGood.properties_template.length) {
            this.selectedAllGood.properties_template.forEach((property) => {
                const d = {
                    name: property.name,
                    value: '',
                    type: property.type
                };
                const d2 = {
                    name: property.name,
                    value: '',
                    type: property.type
                };

                if (hasProperties) {
                    this.selectedGood.properties.forEach((prop) => {
                        if (property.name.toString() === prop.name.toString()) {
                            d.value = prop.value;
                            d2.value = prop.value;
                        }
                    });
                }

                this.properties.push(d);
                this.selectedProperties.push(d2);
            });
        }
    }

    public getSearchResult(): void {
        if (this.timer != null) {
            clearTimeout(this.timer);
        }

        this.timer = setTimeout(() => {
            this.searching = true;
            this.errorMessage = '';
            this.goodsService.getIAllGoods(this.editForm.get('searchField').value).subscribe((returnedGoods: IAllGood[]) => {
                this.allGoods = returnedGoods;
                this.searching = false;
            }, (error) => {
                this.searching = false;
                this.errorMessage = Constants.handleError(error, this.router);
            });
        }, 400);
    }

    getCategories() {
        this.goodsService.getGoodsCategory().subscribe((categories: IGoodsCategory[]) =>
            this.categories = categories, (error) => Constants.handleError(error, this.router)
        );
    }

    deselectSelectedGood() {
        this.clearAdditionalFields();
    }

    onSubmit(form: { value: any }) {
        const formData = form.value;
        formData.pictures = this.pictureIds;
        formData.properties = JSON.stringify({data: this.properties});

        this.saving = true;
        this.errorMessage = '';
        const url = Constants.url + 'goods/' + this.goodId;
        this.goodsService.updateGood(url, formData).subscribe((res: any) => {
            Constants.succeeded(res.message);
            this.saving = false;
            this.updateNewGoodsDetail(res.data);
        }, (error) => {
            this.errorMessage = Constants.handleError(error, this.router);
            this.saving = false;
        });
    }

    prepareToSaveNewGood() {
        const input = this.newGoodsForm.value;
        input.properties = JSON.stringify({data: this.newProperties});
        return input;
    }

    createNewGoodRecord() {
        if (this.saving) {
            return;
        }
        const formModel = this.prepareToSaveNewGood();
        this.savingNewGoods = true;

        this.errorMessage = '';
        this.goodsService.addNewAllGood(formModel).subscribe((res: any) => {
            Constants.succeeded(res.message);
            this.newGoodsForm.reset();
            this.newProperties = [];
            // this.newGoodsForm.reset();
            // this.newProperties = [];
            this.savingNewGoods = false;
        }, (error) => {
            Constants.handleError(error, this.router, true);
            this.savingNewGoods = false;
        });
    }

    addNewProperty() {
        this.newProperties.push({name: '', type: ''});
    }

    removeProperty(property ?: IGoodPropertyTemplate) {
        this.newProperties.splice(this.newProperties.indexOf(property), 1);
    }

    changeDisplay(type: string) {
        this.displayType = type;
    }

    addProperty(property: IGoodPropertyTemplate) {
        this.properties.push(property);
    }

    onDelete(event: Event) {
        event.preventDefault();
        this.goodsService.deleteGood(this.originalVendorGood.id).subscribe((status: boolean) => {
            if (status) {
                Constants.succeeded('Goods has been deleted!');
                setTimeout(() => {
                    this.router.navigate(['app/goods']);
                }, 1500);
            } else {
                this.errorMessage = 'Unable to delete Product';
            }
        }, (err) => {
            this.errorMessage = Constants.handleError(err, this.router);
        });
    }

    deleteGood() {
        Swal.fire({
            title: 'Confirm action',
            text: 'Do you want to delete this record?',
            type: 'question',
            cancelButtonText: 'Cancel',
            showCancelButton: true,
            confirmButtonText: 'Yes Delete',
            showConfirmButton: true
        }).then((willDelete) => {
            if (willDelete.value) {
                this.goodsService.deleteGood(this.originalVendorGood.id).subscribe(deleted => {
                        Constants.succeeded('Goods has been deleted!');
                        setTimeout(() => {
                            this.router.navigate(['/app/vendors/goods']);
                        }, 1500)
                    }, (err) => {
                        Constants.handleError(err, this.router, true);
                    }
                )
            }
        });
    }

    showProperties(cCategory) {
        const list = [];
        if (cCategory) {
            this.categories.forEach(category => {
                if (category.id.toString() === cCategory.toString()) {
                    category.properties_template.forEach(t => {
                        list.push(t.name);
                    });
                }
            });
        }

        let val = '';
        if (list.length > 0) {
            val = '<small class="mt-2">Properties: <strong>' + list.join(', ') + '</strong></small>';
        }
        this.propertyVal = val;
    }

    deleteImage(image, index) {
        this.errorMessage = '';
        Swal.fire({
            title: 'Confirmation',
            text: 'Do you want to remove the selected goods picture?<br>Note: Changes will be effected after the update button is clicked.',
            type: 'question',
            cancelButtonText: 'Cancel',
            showCancelButton: true,
            confirmButtonText: 'Yes Delete',
            showConfirmButton: true
        }).then(willDelete => {
            if (willDelete.value) {
                this.pictures.splice(index, 1);
                this.pictureIds.splice(index, 1);
            }
        })
    }

}
