import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {AppData} from '../../data';
import {Constants} from '../../app.constants';
import {HttpClient, HttpEventType} from '@angular/common/http';
import {IGood, IPicture} from '../../interfaces/interfaces';
import {Router} from '@angular/router';
import {saveAs} from 'file-saver';

@Component({
  selector: 'app-batch-upload',
  templateUrl: './batch-upload.component.html',
  styleUrls: ['./batch-upload.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class BatchUploadComponent implements OnInit {

  fileName = '';

  public progress: number;
  uploading = {status: false, downloading: false};
  loadingGoods = {status: false, error: ''};
  selectedFile: any = null;
  selectedType = 'goods';
  fileUrl = null;
  goods: IGood[] = [];

  constructor(private http: HttpClient, private router: Router) {
  }

  ngOnInit() {
    this.loadGoods();
  }

  public processFile(event) {
    const oldEvent = event;
    const files = event.target.files;
    this.fileName = '';
    this.selectedFile = null;
    if (files.length === 0) {
      return;
    }

    const fileToUpload = <File>files[0];
    this.fileName = fileToUpload.name;
    this.selectedFile = fileToUpload;
  }

  triggerUpload() {
    document.getElementById('file-select').click();
  }

  loadGoods() {
    this.loadingGoods.status = true;
    this.loadingGoods.error = '';
    this.goods = [];
    this.http.get(Constants.url + 'goods/batch-upload/prices', Constants.getAuthHeaderOption()).subscribe((response: any) => {
      this.goods = response;
      this.loadingGoods.status = false;
    }, (error) => {
      this.loadingGoods.status = false;
      this.loadingGoods.error = Constants.handleError(error, this.router, false);
    });
  }

  downloadSample() {
    this.uploading.downloading = true;
    this.http.post(Constants.url + 'goods/batch-upload/sample', {
      type: this.selectedType
    }, {
      headers: Constants.getUploadHeader(),
      responseType: 'blob'
    }).subscribe((response: any) => {
      const blob = new Blob([response], {type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'});
      saveAs(blob);
      this.uploading.downloading = false;
    }, (error) => {
      this.uploading.downloading = false;
      Constants.handleError(error, this.router, true);
    });
  }

  public doUpload() {
    const fileToUpload = this.selectedFile;
    const formData = new FormData();
    formData.append('file', fileToUpload, fileToUpload.name);
    formData.append('type', this.selectedType);
    if (this.selectedType === 'price') {
      formData.append('goods', this.selectedGoods.join(','));
    }

    this.uploading.status = true;

    this.http.post(Constants.url + 'goods/batch-upload', formData,
      {reportProgress: true, observe: 'events', headers: Constants.getUploadHeader()}).subscribe((evt: any) => {
      if (evt.type === HttpEventType.UploadProgress) {
        this.progress = Math.round(100 * evt.loaded / evt.total);
      } else if (evt.type === HttpEventType.Response) {
        const response = evt.body;
        console.log(evt);
        Constants.succeeded(response.message, 'Upload successful');
        this.uploading.status = false;
      }
    }, (error) => {
      this.uploading.status = false;
      Constants.handleError(error, this.router, true);
    });
  }

  get selectedGoods() {
    const data = [];
    this.goods.filter(e => e.selected).forEach(e => data.push(e.id));
    return data;
  }

}
