import {Component, ElementRef, OnInit, ViewEncapsulation} from '@angular/core';
import {Constants} from '../../../app.constants';
import Swal from 'sweetalert2';
import {IGood, IGoodsCategory, RVendor} from '../../../interfaces/interfaces';
import {AuthService} from '../../../services/auth.service';
import {HttpClient} from '@angular/common/http';
import {ActivatedRoute, Router} from '@angular/router';
import {FormBuilder, FormGroup} from '@angular/forms';
import {VendorGoodsService} from '../../../services/vendor-goods.service';
import {AppData} from '../../../data';

declare var $: any;

@Component({
    selector: 'app-vendor-goods-list',
    templateUrl: './vendor-goods-list.component.html',
    styleUrls: ['./vendor-goods-list.component.scss'],
    encapsulation: ViewEncapsulation.None
})
export class VendorGoodsListComponent implements OnInit {

    public searchForm: FormGroup;
    public vendors: RVendor [] = [];
    public goods: IGood[] = [];
    public categories: IGoodsCategory[] = [];
    public good: IGood;
    public displayType = 'list';
    public searchText: string;
    public p: any;
    IGood: any;
    selectedAll: any;

    url = Constants.url;
    current_page: number;
    from: number;
    last_page: number;
    next_page_url: string;
    path: string;
    per_page: number;
    prev_page_url: string;
    to: number;
    total: number;
    checked?: boolean;
    errorMessage: string;
    private selectedGoods: IGood[] = [];
    hasSomeItemsChecked = false;
    searching = false;
    loading = false;
    deleting = false;

    canViewVendorGoods = false;


    constructor(private formBuilder: FormBuilder, public goodsService: VendorGoodsService,
                private route: ActivatedRoute,
                private router: Router,
                private elem: ElementRef,
                public http: HttpClient,
                private authService: AuthService) {
    }

    get processing() {
        return this.searching || this.deleting || this.loading;
    }

    get isVendor() {
        return (Constants.getAuthenticatedUser() || {}).r === AppData.roles.vendor;
    }

    get permissions() {
        return AppData.permissions;
    }

    can(permission) {
        return AuthService.can(permission);
    }

    ngOnInit() {
        this.buildForm();
        if (!this.isVendor) {
            this.loadVendors();
        }

        this.loadCategories();
        this.submitSearch()
    }

    private loadCategories() {
        this.goodsService.getGoodsCategory().subscribe((categories: IGoodsCategory[]) => {
            this.categories = categories;
        }, (err) => {
            this.errorMessage = Constants.handleError(err, this.router);
        });
    }

    loadVendors() {
        this.goodsService.getVendors().subscribe((vendors: RVendor[]) => {
            this.vendors = vendors;
        }, (err) => {
            this.errorMessage = Constants.handleError(err, this.router);
        });
    }

    submitSearch() {
        const searchText = this.searchForm.get('searchField').value;
        const fromDate = this.searchForm.get('fromDate').value;
        const toDate = this.searchForm.get('toDate').value;
        const category = this.searchForm.get('category').value;
        let vendor = '';
        if (!this.isVendor) {
            vendor = this.searchForm.get('vendor').value;
        }

        let param = '';
        if (searchText) {
            param += 'search=' + searchText;
        }

        if (fromDate) {
            if (param) {
                param += '&';
            }
            param += 'from_date=' + this.getTextFromDate(fromDate);
        }

        if (toDate) {
            if (param) {
                param += '&';
            }
            param += 'to_date=' + this.getTextFromDate(fromDate);
        }

        if (!this.isVendor && vendor) {
            if (param) {
                param += '&';
            }
            param += 'vendor=' + vendor;
        }

        if (category) {
            if (param) {
                param += '&';
            }
            param += 'category=' + category;
        }

        const perPage = $('#per-page-select').val();

        if (parseInt(perPage, 10)) {
            if (param) {
                param += '&';
            }
            param += 'per_page=' + perPage;
        }

        if (param) {
            param = '?' + param;
        }

        this.searchGoods('', param);
    }

    searchGoods(url, params) {
        this.searching = true;
        this.errorMessage = '';

        this.goods = [];
        this.goodsService.searchGoods(url, params).subscribe((data: any) => {

                const arr = ((typeof data.data) !== 'undefined' && data.data.length > 0);
                if (arr) {
                    data.data.forEach(d => d.selected = false);
                }
                this.goods = data.data;
                this.current_page = data.current_page;
                this.from = data.from;
                this.last_page = data.last_page;
                this.next_page_url = data.next_page_url;
                this.path = data.path;
                this.prev_page_url = data.prev_page_url;
                this.per_page = data.per_page;
                this.to = data.to;
                this.total = data.total;

                this.searching = false;
            }, (err) => {
                this.errorMessage = Constants.handleError(err, this.router);
                this.searching = false;
            }
        )
    }

    private buildForm() {
        this.searchForm = this.formBuilder.group({
            searchField: [''],
            fromDate: [''],
            toDate: [''],
            vendor: [''],
            category: ['']
        });
    }

    deleteSingleGood(good, i) {
        Swal.fire({
            title: 'Confirm delete',
            text: 'Do you want to delete this goods item?',
            type: 'question',
            cancelButtonText: 'Cancel',
            showCancelButton: true,
            confirmButtonText: 'Yes Delete',
            showConfirmButton: true
        }).then(willDelete => {
            if (willDelete.value) {
                this.deleting = true;
                this.goodsService.deleteGood(good.id).subscribe((response) => {
                    this.goods.splice(this.goods.indexOf(good), 1);
                    this.deleting = false;
                }, (err) => {
                    Constants.handleError(err, this.router, true);
                    this.deleting = false;
                });
            }
        })
    }

    updateSelectionChanged() {
        let hasSomeItemsChecked = 0;
        this.goods.forEach(x => {
            if (x.selected) {
                hasSomeItemsChecked++;
            }
        });

        this.hasSomeItemsChecked = hasSomeItemsChecked > 1;
    }

    checkAll(ev) {
        this.goods.forEach(x => x.selected = ev.target.checked);
        this.updateSelectionChanged()
    }

    isAllChecked() {
        return this.goods.every(_ => _.selected);
    }

    public toggle(type) {
        this.displayType = type;
    }

    deleteSelectedGoods() {
        this.selectedGoods = this.goods.filter(_ => _.selected);
        if (this.selectedGoods == null || this.selectedGoods.length < 1) {
            return;
        }
        Swal.fire({
            title: 'Confirm delete',
            text: 'Do you want to delete the selected goods items (' + this.selectedGoods.length + ') ?',
            type: 'question',
            cancelButtonText: 'Cancel',
            showCancelButton: true,
            confirmButtonText: 'Yes Delete',
            showConfirmButton: true
        }).then(willDelete => {
            if (willDelete.value) {
                const selectedItemIds = [];
                for (const item of this.selectedGoods) {
                    selectedItemIds.push(item.id);
                }

                this.errorMessage = '';
                this.deleting = true;
                this.goodsService.deleteGoods(selectedItemIds).subscribe(data => {
                        for (const item of this.selectedGoods) {
                            this.selectedGoods.splice(this.selectedGoods.indexOf(item), 1);
                        }
                        this.deleting = false;
                    }, (err) => {
                        this.errorMessage = Constants.handleError(err, this.router);
                        this.deleting = false;
                    }
                )
            }
        })
    }

    private getTextFromDate(date: any) {
        if (typeof date === 'object') {
            return date.year + '-' + date.month + '-' + date.day;
        }
        return date.toString();
    }

    public getBatchGood() {
        const options = Constants.getAuthHeaderOption();
        const url = Constants.url + 'goods/import-sample/excel';
        return this.http.get(url, options)
    }

    // onFileChange(event) {
    //     if (event.target.files.length > 0) {
    //         const zip = event.target.files[0];
    //         this.searchForm.get('pictures').setValue(zip);
    //     }
    //     if (event.target.files.length > 0) {
    //         const file = event.target.files[0];
    //         this.searchForm.get('file').setValue(file);
    //     }
    // }

}
