import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {VendorGoodsListComponent} from './vendor-goods-list/vendor-goods-list.component';
import {CreateVendorGoodsComponent} from './create-vendor-goods/create-vendor-goods.component';
import {EditVendorGoodsComponent} from './edit-vendor-goods/edit-vendor-goods.component';
import {ViewVendorGoodsComponent} from './view-vendor-goods/view-vendor-goods.component';
import {PerfectScrollbarModule} from 'ngx-perfect-scrollbar';
import {RouterModule, Routes} from '@angular/router';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';
import {UploadModule} from '../../shared/upload/upload.module';
import {LoaderModule} from '../../shared/loader/loader.module';
import {CustomErrorModule} from '../../shared/custom-error/custom-error.module';
import {NgxChartsModule} from '@swimlane/ngx-charts';
import {SurveyModule} from '../../shared/survey/survey.module';
import {BatchUploadModule} from '../../shared/batch-upload/batch-upload.module';

export const routes: Routes = [
    {path: 'create', component: CreateVendorGoodsComponent},
    {path: 'edit/:id', component: EditVendorGoodsComponent},
    {path: 'view/:id', component: ViewVendorGoodsComponent},
    {path: '', redirectTo: '/app/vendors/goods/list', pathMatch: 'full'},
    {path: 'list', component: VendorGoodsListComponent},
];

@NgModule({
    declarations: [
        VendorGoodsListComponent,
        CreateVendorGoodsComponent,
        EditVendorGoodsComponent,
        ViewVendorGoodsComponent
    ],
  imports: [
    CommonModule,
    PerfectScrollbarModule,
    RouterModule.forChild(routes),
    FormsModule,
    ReactiveFormsModule,
    CustomErrorModule, LoaderModule,
    UploadModule,
    NgxChartsModule,
    SurveyModule, BatchUploadModule
  ]
})
export class VendorGoodsModule {
}
